

import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import tools from '../../lib/tools.js';
export default {
name:'doctorIndex',
components:{Flexbox, FlexboxItem,XButton,Toast},
data(){
return {
id:'',
toastMsg:'',
showToast:false,
info:{},
serviceArr:[],

}
},
mounted(){
this.loading();
this.loadService();
this.wxConfig();
},
methods:{
loading(){
var vm = this;
let id = tools.getRequestParam('md'); 
if(!id){return false;}
this.id = id;
this.$http.post('/backstage/queryOne?managerId='+id,{}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
if(data.data){
vm.info = data.data;
}
}else{
vm.showToast = true;
vm.toastMsg = '查询失败';
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '查询失败';
});
},
loadService(){
var vm = this;
let id = tools.getRequestParam('md');
if(!id){return false;}
this.id = id;
this.$http.post('/service/query?id='+id+'&status=2&pageNumber=1&pageSize=1000&serviceAttribute=1',{}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
if(data.items){
vm.serviceArr = data.items;
}
}else{
vm.showToast = true;
vm.toastMsg = '查询失败';
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '查询失败';
});
},
goTo(id){


tools.next("/m/serviceIndex.html?serviceId="+id);


},
wxConfig(){
let _this = this;
let config = {
jsList:['onMenuShareTimeline','onMenuShareAppMessage'],
url:location.href.split('#')[0]
}
let url = '';
let id = localStorage.getItem('md') || tools.getCookie('user_id');
url = id?'&sourceUserId='+id:'';
tools.wxConfig(this.$http,config,(result) => {
wx.ready(function() {
let shareData = {
title: _this.info.name,
desc: _this.info.description,
link: result.link+ 'm/sharing.html' + '?type=2&id='+_this.id+url,
imgUrl: result.link+ 'm/static/img/20170817165354.jpg',
};
console.log('shareData==',shareData);
wx.onMenuShareAppMessage(shareData);
wx.onMenuShareTimeline(shareData);
});
});
},
applyToService(){
var vm = this;
let managerId = localStorage.getItem('md') || tools.getCookie('user_id');
let doctorId = tools.getRequestParam('md');
if(managerId){

this.$http.axios({method:'POST',url:'/api/contracte/queryRel',data:{managerId:managerId,doctorId:doctorId}}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
if(data.data && data.data.length>0){
vm.showToast = true;
vm.toastMsg = '您已申请过,请勿重复申请';
return false;
}else{
console.log(111)
tools.next('/m/applyService.html?doctorId='+vm.id);
}
}else{
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
console.error(data);
return false;
}
},(error) => {
console.error(error);
return false;
})
return false;
}else{
console.log(333)
tools.next('/m/applyService.html?doctorId='+this.id);
}
}
}
}



